<template>
  <form @submit.prevent="onSubmit">
    <input v-model="form.titulo"   placeholder="Título" required />
    <input v-model="form.autor"  placeholder="Autor" />
    <input v-model.number="form.preco" type="number" placeholder="Preço" />
    <button type="submit">Adicionar Livro</button>
  </form>
</template>

<script>
import { createBook } from '@/services/bookService'

export default {
  name: 'AddBookForm',
  data() {
    return {
      form: { titulo: '', autor: '', preco: 0 }
    }
  },
  methods: {
    async onSubmit() {
      try {
        await createBook(this.form)
        this.$emit('book-added')
        this.form = { titulo: '', autor: '', preco: 0 }
      } catch (e) {
        console.error('Erro ao adicionar:', e)
      }
    }
  }
}
</script>