import axios from 'axios'
const API = axios.create({ baseURL: 'http://localhost:3000/announcements' })

export const fetchAllAnnouncements = ()    => API.get('/')
export const fetchMyAnnouncements  = id   => API.get(`/mine/${id}`)
export const createAnnouncement    = (livro, user) => API.post('/', { id_livro: livro, id_usuario: user })